<template>
  <el-card>
    <el-form ref="form" :model="form" v-bind="$attrs">
      <el-row :gutter="10">
        <el-col
          v-for="(item, index) in formItemList"
          v-show="index <= firstIndex || expandType"
          :key="index"
          :span="item.span || 6"
        >
          <el-form-item :label="item.label" :prop="item.model">
            <!-- input -->
            <el-input
              v-if="item.type === 'input'"
              v-model="form[item.model]"
              :placeholder="item.placeholder || ''"
              clearable
            />
            <!-- select initialValueoptionsselect -->
            <el-select
              v-if="item.type === 'select'"
              v-model="form[item.model]"
              class="width_100"
              :multiple="Array.isArray(item.initialValue || '')"
              collapse-tags
              clearable
              placeholder=""
            >
              <el-option
                v-for="itemSub in item.options"
                :key="itemSub.value"
                :label="itemSub.label"
                :value="itemSub.value"
              ></el-option>
            </el-select>
            <!--  -->
            <el-date-picker
              v-if="item.type === 'date'"
              v-model="form[item.model]"
              class="width_100"
              :type="item.attrs.type"
              :format="item.attrs.format"
              :value-format="item.attrs.valueFormat"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
            ></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col class="button_box" :span="6">
          <el-form-item label-width="0" class="button_position">
            <el-button type="primary" @click="handleSearch"></el-button>
            <el-button @click="handleReset"></el-button>
            <el-button
              v-show="spanLength >= 24 * row"
              type="text"
              @click="openForm"
            >
              <span v-if="expandType"></span>
              <span v-else></span>
              <i v-if="expandType" class="el-icon-arrow-up"></i>
              <i v-else class="el-icon-arrow-down"></i>
            </el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <section v-if="!expandType && hideFormList.length > 0" class="form_footer">
      <span></span>
      <el-tag
        v-for="(tag, index) in hideFormList"
        :key="index"
        size="mini"
        closable
        @close="closeTag(tag)"
      >
        <span v-if="tag.type === 'input'">
          {{ tag.label }}: {{ tag.value }}
        </span>
        <span v-else-if="tag.type === 'select'">
          {{ tag.label }}: {{ tag.value.map((item) => item.label).toString() }}
        </span>
        <span v-else-if="tag.type === 'date'">
          {{ tag.label }}: {{ tag.value[0] }}  {{ tag.value[1] }}
        </span>
      </el-tag>
    </section>
  </el-card>
</template>

<script>
export default {
  name: 'search-form-config',
  props: {
    formItemList: {
      // 
      type: Array,
      default: () => []
    },
    row: {
      // 
      type: Number,
      default: 1
    }
  },
  data () {
    return {
      form: {},
      expandType: false,
      itemList: [],
      firstIndex: 0, // 
      spanLength: 0,
      hideFormList: []
    }
  },
  mounted () {
    // span
    this.spanLength = 0
    // this.form
    const formKey = {}
    this.formItemList.forEach((item, index) => {
      this.spanLength += item.span || 6
      formKey[item.model] = item.initialValue || ''
      if (this.spanLength < 24 * this.row) {
        this.firstIndex = index
      }
    })
    this.form = formKey
  },
  methods: {
    handleSearch () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          console.log(this.form)
          this.expandType = false
          this.getHideData()
        }
      })
    },
    // 
    closeTag (tag) {
      this.hideFormList = this.hideFormList.filter(
        (item) => item.key !== tag.key
      )
      this.resetForm(tag)
    },
    // form
    resetForm (tag) {
      Object.keys(this.form).forEach((item) => {
        if (item === tag.key) {
          if (typeof this.form[item] === 'string') {
            this.$set(this.form, item, '')
          } else if (this.form[item] instanceof Array) {
            this.$set(this.form, item, [])
          }
        }
      })
    },
    // 
    getHideData () {
      this.hideFormList = []
      Object.keys(this.form).forEach((item, index) => {
        if (index > this.firstIndex) {
          this.formItemList.forEach((formItem) => {
            if (
              item === formItem.model &&
                typeof this.form[item] === 'string' &&
                this.form[item] !== ''
            ) {
              this.hideFormList.push({
                type: formItem.type,
                key: item,
                value: this.form[item],
                label: formItem.label
              })
            } else if (
              item === formItem.model &&
                typeof this.form[item] === 'object' &&
                Array.isArray(this.form[item]) &&
                this.form[item].length > 0
            ) {
              const valueArr = []
              if (formItem.type === 'select') {
                // selectlabel
                formItem.options.forEach((option) => {
                  this.form[item].forEach((valueItem) => {
                    if (option.value === valueItem) {
                      valueArr.push({ label: option.label, value: valueItem })
                    }
                  })
                })
                this.hideFormList.push({
                  type: formItem.type,
                  key: item,
                  value: valueArr,
                  label: formItem.label
                })
              } else if (formItem.type === 'date') {
                // date
                this.hideFormList.push({
                  type: formItem.type,
                  key: item,
                  value: this.form[item],
                  label: formItem.label
                })
              }
            }
          })
        }
      })
    },
    // ,itemList
    openForm () {
      this.expandType = !this.expandType
      this.getHideData()
    },
    // 
    handleReset () {
      const formKey = {}
      this.formItemList.forEach((item) => {
        formKey[item.model] = item.initialValue || ''
      })
      this.hideFormList = []
      this.form = formKey
    }
  }
}
</script>

<style lang="scss">
  .button_box {
    float: right;
    .button_position {
      display: flex;
      justify-content: right;
    }
  }
  .width_100 {
    width: 100%;
  }
  .form_footer {
    display: flex;
    align-items: center;
    width: calc(100% - 20px);
    padding: 10px;
    overflow: hidden;
    background: rgba(37, 42, 61, 0.03);
    border: 1px #c0c4cc dashed;
  }
</style>
